import * as React from 'react';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimeLineItem';
import TimelineSeparator from '@mui/lab/TimeLineSeparator';
import TimelineConnector from '@mui/lab/TimeLineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import TimelineDot from '@mui/lab/TimelineDot';
import Layout from '../components/layout';
import { Typography } from '@mui/material';

const IndexPage = () => {
  return(
    <Layout pageTitle='About'>
      <h1>About Me</h1>
      <p>Hello, I am Yihua Liu. My Chinese name is 刘翊华 (pinyin: liú yì huá; IPA: l̠j͗o̟͑ʊ̯˧˥ iᶨ˥˩ x̞wä˧˥).</p>
      <p>I am a first-year graduate student at University of Michigan - Shanghai Jiao Tong University Joint Institute, major in Electronic Science and Technology. I am interested in hardware/software co-design.</p>
      <h1>Education</h1>
      <div style={{height: '100vh', fontFamily: 'Merriweather Sans'}}>
        <Timeline>
          <TimelineItem>
            <TimelineOppositeContent
              sx={{ m: 'auto 0'}}
              align="right"
              variant="subtitle1"
              color="text.primary"
            >
              Sept. 2022 - Mar. 2025
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot color="success"></TimelineDot>
              <TimelineConnector/>
            </TimelineSeparator>
            <TimelineContent sx={{ py: '15pt', px: 2}}>
              <Typography variant="h5" component="span">
                Shanghai Jiao Tong University
              </Typography>
              <Typography variant="body1">
                University of Michigan - Shanghai Jiao Tong University Joint Institute (UM-SJTU JI)
              </Typography>
              <Typography variant="h6">
                Master, Electronic Science and Technology
              </Typography>
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent
              sx={{ m: 'auto 0'}}
              align="right"
              variant="subtitle1"
              color="text.primary"
            >
              Sept. 2018 - Aug. 2022
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot color="success"></TimelineDot>
              <TimelineConnector/>
            </TimelineSeparator>
            <TimelineContent sx={{ py: '15pt', px: 2}}>
              <Typography variant="h5" component="span">
                Shanghai Jiao Tong University
              </Typography>
              <Typography variant="body1">
                University of Michigan - Shanghai Jiao Tong University Joint Institute (UM-SJTU JI)
              </Typography>
              <Typography variant="h6">
                B.S. in Electrical and Computer Engineering
              </Typography>
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent
              sx={{ m: 'auto 0'}}
              align="right"
              variant="body1"
              color="text.primary"
            >
              Sept. 2012 - June 2018
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot color="success"></TimelineDot>
              <TimelineConnector/>
            </TimelineSeparator>
            <TimelineContent sx={{ py: '15pt', px: 2}}>
              <Typography variant="h5" component="span">
                The Affiliated High School of Shanxi University
              </Typography>
            </TimelineContent>
          </TimelineItem>
        </Timeline>
      </div>
    </Layout>
  )
}

export default IndexPage
